@import '../../styles/vars.scss';

.swap-form-remove {
  max-width: 480px;
  width: 100%;
  background: rgb(25, 27, 31) none repeat scroll 0% 0%;
  box-shadow: rgba(0, 0, 0, 0.01) 0px 0px 1px, rgba(0, 0, 0, 0.04) 0px 4px 8px, rgba(0, 0, 0, 0.04) 0px 16px 24px, rgba(0, 0, 0, 0.01) 0px 24px 32px;
  border-radius: 24px;
  margin: 1rem auto;
  padding: 8px;

  &__label-wrapper {
    display: flex;
    flex-direction: row;
    width: 100%;
    justify-content: space-between;
    .select-wrapper {
      z-index: 5;
    }
  }

  .select-wrapper {
    position: static;
  }

  &__header {
    color: white;
    font-weight: 500;
    font-size: 1rem;
  }

  &__label {
    display: flex;
    flex-flow: column nowrap;
    position: relative;
    border-radius: 20px;
    background-color: rgb(44, 47, 54);
    z-index: 1;
    width: 100%;
    min-height: 72px;
    color: white;
    text-align: center;

  }


  &__input {
    border-radius: 20px;
    background-color: rgb(44, 47, 54);
    width: 100%;
    height: 72px;
    padding-left: 8px;
    color: white;
    font-size: 1.75rem;
    padding-right: 38%;
  }

  &__input::placeholder {
    color: white;
  }

  &__form {
    display: flex;
    flex-direction: column;
    gap: 24px;
    align-items: center;
  }

  &__arrow{
    width: 32px;
    height: 32px;
  }

  &__error{
    color: red;
    padding: 2px;
    text-align: center;
  }

  &__balance {
    color: white;
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    padding: 8px;
  }

}

@media (max-width:500px) {
  .swap-form {
    &__input {
      font-size: 1.2rem;
    }
  }
}